<script setup lang="ts">
    import { GpnButton, GpnCheckbox, GpnTextField, type NotifPlacement, type NotifView, useNotify } from "gpn-ui";
    import { useGlobalStore } from "../stores/store";
    import { ref } from "vue";

    const store = useGlobalStore();
    const notify = useNotify();
    const message = ref("Связь успешно удалена");
    const title = ref("Внимание");
    const dur = ref(5000);
    const closable = ref(true);
    const withBtn = ref(false);
    function createNotify(viewType: NotifView, placement: NotifPlacement) {
        const notifyObj = {
            message: message.value,
            placement: placement,
            duration: dur.value,
            view: viewType,
            title: title.value,
            closable: closable.value,
        };
        if (withBtn.value) {
            Object.assign(notifyObj, {
                buttonLabel: "Нажми меня",
                buttonHandler: () => {
                    alert("Some message");
                },
            });
        }
        notify?.create(notifyObj);
    }
</script>

<template>
    <h2>Notify</h2>
    <div style="display: flex; align-items: center; gap: 16px">
        <GpnTextField v-model="dur" type="number" label="Длительность, мс" :theme="store.theme" />
        <GpnTextField v-model="title" label="Заголовок" :theme="store.theme" />
        <div style="flex-basis: 400px; display: flex"><GpnTextField v-model="message" label="Текст" :theme="store.theme" width="full" /></div>
        <div style="padding-top: 18px">
            <GpnCheckbox v-model="closable" label="С опцией закрытия" :theme="store.theme" />
        </div>
        <div style="padding-top: 18px">
            <GpnCheckbox v-model="withBtn" label="С кнопкой" :theme="store.theme" />
        </div>
    </div>
    <div class="grid">
        <h5>type</h5>
        <div>info</div>
        <div>primary</div>
        <div>success</div>
        <div>warning</div>
        <div>error</div>

        <h5>left-top</h5>
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('info', 'left-top')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('primary', 'left-top')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('success', 'left-top')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('warning', 'left-top')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('error', 'left-top')" />
        <h5>center-top</h5>
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('info', 'center-top')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('primary', 'center-top')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('success', 'center-top')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('warning', 'center-top')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('error', 'center-top')" />
        <h5>right-top</h5>
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('info', 'right-top')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('primary', 'right-top')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('success', 'right-top')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('warning', 'right-top')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('error', 'right-top')" />

        <h5>left-bottom</h5>
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('info', 'left-bottom')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('primary', 'left-bottom')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('success', 'left-bottom')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('warning', 'left-bottom')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('error', 'left-bottom')" />
        <h5>center-bottom</h5>
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('info', 'center-bottom')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('primary', 'center-bottom')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('success', 'center-bottom')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('warning', 'center-bottom')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('error', 'center-bottom')" />
        <h5>right-bottom</h5>
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('info', 'right-bottom')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('primary', 'right-bottom')" />
        <GpnButton label="Create notify" :theme="store.theme" @click="createNotify('success', 'right-bottom')" />
